.advantages-color {
  background: #f2f2f2;
  margin-top: -24px;
  padding: 0;
  margin-bottom: 40px;
  list-style: none;
}

.advantages-color__number {
  color: #333333;
  margin-top: -5px;
}

.advantages-color__description {
  color: #333333;
  width: 246px;
  margin-left: 35px;
  border-bottom: rgba(0, 0, 0, 0.1) solid 2px;
  padding-bottom: 25px;
  margin-top: 16px;
}

.advantages-color__description2 {
  color: #333333;
  width: 246px;
  margin-left: 35px;
  border-bottom: rgba(0, 0, 0, 0.1) solid 2px;
  padding-bottom: 24px;
  margin-top: 10px;
}

.advantages-color__item {
  margin-bottom: -20px;
  padding-top: 20px;
  margin-top: 11px;
}

.advantages-color__description3 {
  color: #333333;
  width: 252px;
  margin-left: 33px;
  padding-bottom: 25px;
}

.advantages-color__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

@media (min-width: 768px) {
  .advantages-color__description2,
  .advantages-color__description {
    border-bottom: none;
  }

  .advantages-color {
    display: flex;
  }

  .advantages-color__title {
    width: 159px;
    font-size: 21px;
    margin-left: 48px;
    margin-right: 49px;
    margin-top: 30px;
    margin-bottom: 0;
  }

  .advantages-color__description {
    width: 159px;
    margin-left: 49px;
  }

  .advantages-color__description2 {
    width: 132px;
    margin-left: 62px;
  }

  .advantages-color__description3 {
    width: 149px;
    margin-left: 54px;
  }

  .advantages-color__number {
    margin-top: 12px;
    margin-bottom: 20px;
  }

  .advantages-color__item {
    padding-bottom: 27px;
  }
}

@media (min-width: 1200px) {
  .advantages-color__description {
    width: 285px;
    margin-left: 55px;
    margin-top: 21px;
  }

  .advantages-color__description3 {
    width: 260px;
    margin-left: 71px;
    margin-top: 22px;
  }

  .advantages-color__description2 {
    width: 262px;
    margin-left: 69px;
    margin-top: 22px;
  }

  .advantages-color__title {
    margin-left: 121px;
    margin-right: 119px;
  }

  .advantages-color__item {
    padding-bottom: 42px;
  }
}
