@media (min-resolution: @retina-dpi),
  (min-resolution: @retina-dppx) {
  .welcom {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }
}

.welcom-phote {
  margin-top: 58px;
  width: 320px;
  height: 159px;
  background-color: aquamarine;
  background-image: url("../img/intro-triangle-svg.svg"), url("../img/back-mobile-jpg.jpg");
  background-position: bottom center, center center;
  background-repeat: no-repeat;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.welcom-phote__img2 {
  padding-top: 50px;
}

@media (min-width: 768px) {
  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-tablet@2x.jpg");
  }

  .welcom-phote {
    margin-top: 15px;
    width: 768px;
    height: 290px;
    background-image: url("../img/bg-intro-triangle-tablet.svg"), url("../img/bg-back-tablet@1x.jpg");
    background-size: 100% 57px, 100% auto;
    background-repeat: repeat-x, no-repeat;
    background-position: bottom center, bottom center;
  }

  .welcom-phote__img2 {
    width: 460px;
    height: 110px;
    margin-top: 30px;
  }
}

@media (min-width: 1200px) {
  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-desktop@2x.jpg");
  }

  .welcom-phote {
    width: 100%;
    height: 300px;
    margin-top: 15px;
    background-image: url("../img/bg-intro-triangle-desktop.svg"), url("../img/bg-back-desktop@1x.jpg");
    background-size: 100% 57px, 100% auto;
    background-repeat: repeat-x, no-repeat;
    background-position: bottom center, bottom center;
  }

  .welcom-phote__img2 {
    width: 460px;
    height: 122px;
  }
}
