.bridge {
  padding-top: 25px;
  background: #81b3d2;
  color: white;
  width: 320px;
  padding-bottom: 24px;
  margin-top: 35px;
}

.bridge__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

.bridge__tourist {
  font-size: 14px;
  line-height: 21px;
  margin-left: 30px;
  margin-right: 31px;
}

.bridge__number {
  margin-top: -5px;
}

@media (min-width: 768px) {
  .bridge {
    order: 2;
    width: 256px;
    height: 256px;
    padding: 0;
    margin-top: 20px;
  }

  .bridge__title {
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    width: 151px;
    margin-top: 45px;
    margin-left: 52px;
  }

  .bridge__number {
    margin-top: -7px;
    margin-bottom: 18px;
  }
}

@media (min-width: 1200px) {
  .bridge {
    width: 33.333%;
    margin-top: 40px;
  }

  .bridge__title {
    margin-top: 53px;
    margin-left: 122px;
  }

  .bridge__tourist {
    width: 220px;
    margin-left: 90px;
    margin-top: 11px;
  }
}
